import React, { ComponentType } from 'react';
import './profile.less';
import { cappedImageUrl } from '../../common/images';
import { ComponentModel } from '../../common/models';
import { withContentful } from '../../connectors/with-contentful';

export const COMPONENT_PROFILE_CONTENT_TYPE = 'profile';

export interface ProfileModel extends ComponentModel {
  backgroundImage?: any; // TODO add own image type
  name?: string;
  instagramHandle?: string;
  bio?: string;
  backgroundColor?: string;
}

export class ProfileComponent extends React.Component<ProfileModel, any> {
  render() {
    const { backgroundImage, backgroundColor, name, instagramHandle, bio } = this.props;
    const backgroundImageUrl = backgroundImage ? `url('${cappedImageUrl(backgroundImage.file.url)}')` : 'none';
    const style = {
      backgroundImage: backgroundImageUrl ? backgroundImageUrl : 'none',
      backgroundColor: backgroundColor ? 'inherit' : backgroundColor,
    };

    return (
      <div>
        <div className="te-profile" style={style}>
          <h2 className="te-profile-title">{name}</h2>
          <p className="te-profile-handle">{instagramHandle}</p>
        </div>
        <p className="te-profile-bio">{bio}</p>
      </div>
    );
  }
}

export const Profile: ComponentType<ProfileModel> = withContentful()(ProfileComponent);
