import React, { ComponentType } from 'react';
import cx from 'classnames';

import { LazyImgCms, ObjectFitImage } from '../../common/lazy-img/lazy-img';
import { ComponentModel } from '../../common/models';
import { withContentful } from '../../connectors/with-contentful';
import { lowQualityCappedImageUrl } from '../../common/images';

import './image.less';

export const COMPONENT_IMAGE_CONTENT_TYPE = 'image';

export interface ImageModel extends ComponentModel {
  image?: any; // TODO add own image type
  imageName?: string;
  link?: string;
  linkText?: string;
  backgroundImage?: boolean;
  dangerousBackgroundImageMode?: boolean;
  backgroundPosition?: string;
  file?: any;
}

export class ImageComponent extends React.Component<ImageModel> {
  getObjectFitValue(): 'cover' | 'contain' {
    const { isInGrid, backgroundImage } = this.props;
    if (isInGrid || backgroundImage) {
      return 'cover';
    }
    return 'contain';
  }

  render() {
    const { imageName, linkText, image, isInGrid, dangerousBackgroundImageMode } = this.props;
    const objectFitValue = this.getObjectFitValue();

    const imgClassNames = cx('te-image-component', {
      'te-image-link': linkText,
      'te-image-component-object-fit': dangerousBackgroundImageMode,
    });

    const imageElement =
      image && isInGrid ? (
        <ObjectFitImage
          fit="cover"
          src={image.file.url}
          alt={imageName}
          loadingPlaceholderSrc={lowQualityCappedImageUrl(image.file.url)}
          className={imgClassNames}
          backgroundPosition={this.props.backgroundPosition}
        />
      ) : (
        <LazyImgCms
          fit={objectFitValue}
          src={image.file.url}
          alt={imageName}
          className={imgClassNames}
          backgroundPosition={this.props.backgroundPosition}
        />
      );

    const linkTextElement = <span>{linkText}</span>;

    return (
      <React.Fragment>
        {imageElement}
        {linkText && <span className="te-image-component-link-text">{linkTextElement}</span>}
      </React.Fragment>
    );
  }
}

export const Image: ComponentType<ImageModel> = withContentful()(ImageComponent);
