import React from 'react';
import { TipserSDK, TipserSdkConfig, TipserSDKInstance, TipserSdkEnv } from '@tipser/tipser-sdk';
import { Context } from 'react';
import omit from 'lodash.omit';
import hoistNonReactStatics from 'hoist-non-react-statics';

import { setLang } from './i18n';
import { SiteModel } from './components/site/site';
import { EventBus, createEventBus, EVENT_DIALOG_CLOSED } from './tipser-events';
import { ErrorPopup } from './tipser-error';
import { TipserProductModel } from './components/product/model';
import { SentryClient } from './common/sentry';

import './theme/index.less';
import { primaryColorManager } from './common/utils';

/**
 * This configuration interface should reflect publicly documented configuration
 */
export interface TipserElementsConfig {
  lang?: string;
  env?: TipserSdkEnv;
  defaultAddedToCartPopup?: boolean;
  useDefaultErrorHandler?: boolean;
  eventsHandlers?: TipserConfigEventHandlers;
  useDeepLinking?: boolean;
  customLabels?: { [key in 'buy' | 'unavailable']?: string };
  primaryColor?: string;
  isWidget?: boolean;
  openOldDialog?: boolean;
  modalUi?: ModalUiOptions;
}

// data which may be stored in context and in config
export interface TipserCommonConfigContextData extends TipserSdkConfig {
  gaTrackingCode?: string;
  ga?: string;
  contentfulApiKey?: string;
  contentfulSpace?: string;
  tipserApiUrl?: string;
  contentfulEnv?: string;
  isGaEnabled?: boolean;
  tipserSdk?: TipserSDKInstance;
  theme?: any;
  isWidget?: boolean;
  cms?: object;
  site?: SiteModel;
  addToCartSettings?: {
    directToChekoutMode: boolean;
  };
  isEmbeddedInShop?: boolean;
  openOldDialog?: boolean;
}

export interface TipserConfigEventHandlers {
  onAddToCart?: (params: { cartSize?: number; product?: TipserProductModel }) => void;
  onError?: () => void;
}

export interface TipserConfigData extends TipserCommonConfigContextData, TipserElementsConfig {}

export interface TipserContextData extends TipserCommonConfigContextData, TipserElementsConfig {
  events?: EventBus;
}

export interface ModalUiOptions {
  hideSearchIcon?: boolean;
  hideFavouritesIcon?: boolean;
  hideCartIcon?: boolean;
  hideMoreIcon?: boolean;
  hideSimilarProducts?: boolean;
  useCustomCss?: boolean;
}

// serves as typed interface for props for components which make use of context
export interface Contextable {
  context?: TipserContextData;
  updateContext?: (value: TipserContextData) => TipserContextData;
}

// default values
const ENV = (process.env.ENV || 'prod') as TipserSdkEnv;
const DEFAULT_CONTENTFUL_API_KEY =
  process.env.CONTENTFUL_API_KEY || 'e969dc3e3878fd07771d8d3fe3a662f56bff942f7c37277d894dbcd241679fe7';
const DEFAULT_CONTENTFUL_SPACE = process.env.CONTENTFUL_SPACE || 'i8t5uby4h6ds';
const DEFAULT_LANG = process.env.LANG || 'en';
const DEFAULT_CONTENTFUL_ENV = process.env.CONTENTFUL_ENV || 'master';
const DEFAULT_IS_GA_ENABLED = process.env.IS_GA_ENABLED || false;
const DEFAULT_THEME = process.env.THEME || {};
const DEFAULT_IS_WIDGET = process.env.IS_WIDGET || false;
const DEFAULT_GA_TRACKING_CODE = 'UA-26837765-10';
const DEFAULT_CMS_CONNECTOR_CONFIG = {
  host: 'cdn.contentful.com',
};
const DEFAULT_ADD_TO_CART_SETTINGS = {
  directToChekoutMode: false,
};
const DEFAULT_DEFAULT_ADDED_TO_CART_POPUP = true;
const DEFAULT_USE_DEEP_LINKING = true;
export const DEFAULT_PRIMARY_COLOR = '#222';

// singleton context instance
let contextInstance;
let sdkInstance;

export const getSdkInstance = () => sdkInstance;

// TODO : there is no option to steer connector factory from context config
export const TipserContext = (): Context<{
  updateContext: (value: object) => TipserContextData;
  data: TipserContextData;
}> => {
  return contextInstance;
};

let contextPreparedData: TipserContextData = undefined;
export const getTipserContextData = () => contextPreparedData;

export interface TipserElementsProviderProps {
  posId: string;
  config?: TipserElementsConfig;
  isSentryEnabled?: boolean;
}

// context provide which allows us to merge predefined context data with new ones
export class TipserElementsProvider extends React.Component<TipserElementsProviderProps, TipserContextData> {
  static defaultProps = {
    config: {},
    isSentryEnabled: true,
  };

  constructor(props: TipserElementsProviderProps) {
    super(props);

    if (props.isSentryEnabled) {
      SentryClient.initialize();
    }

    if (!props.posId) {
      throw new Error('required posId attribute is missing on <TipserElementsProvider/> element');
    }

    const eventBus = createEventBus();

    this.state = {
      ...this.preparePropsForContext(
        {
          posId: props.posId,
          cms: DEFAULT_CMS_CONNECTOR_CONFIG,
          gaTrackingCode: DEFAULT_GA_TRACKING_CODE,
          lang: DEFAULT_LANG,
          env: ENV,
          // TODO : as we have cms config option, maybe we can move contentful options there
          contentfulApiKey: DEFAULT_CONTENTFUL_API_KEY,
          contentfulSpace: DEFAULT_CONTENTFUL_SPACE,
          site: null,
          contentfulEnv: DEFAULT_CONTENTFUL_ENV,
          isGaEnabled: !!DEFAULT_IS_GA_ENABLED,
          theme: DEFAULT_THEME,
          isWidget: !!DEFAULT_IS_WIDGET,
          addToCartSettings: DEFAULT_ADD_TO_CART_SETTINGS,
          events: eventBus,
          useDefaultErrorHandler: DEFAULT_USE_DEEP_LINKING,
          useDeepLinking: true,
          defaultAddedToCartPopup: DEFAULT_DEFAULT_ADDED_TO_CART_POPUP,
          customLabels: {},
          primaryColor: null,
        },
        props.config
      ),
    };

    if (props.config.eventsHandlers) {
      Object.entries(props.config.eventsHandlers).map(([event, handler]) => {
        if (Array.isArray(handler)) {
          handler.map((eventHandler) => eventBus.register(event, eventHandler));
        } else {
          eventBus.register(event, handler);
        }
      });
    }

    if (!contextInstance) {
      contextInstance = React.createContext(this.state);
    }
  }

  componentDidMount() {
    if (this.state.primaryColor) {
      primaryColorManager(this.state.primaryColor).load();
    }
  }

  componentWillUnmount() {
    if (this.state.primaryColor) {
      primaryColorManager(this.state.primaryColor).unload();
    }
  }

  onDialogClosed = () => {
    sdkInstance.getCurrentCartSize().then(
      (cartSize) => {
        this.state.events.dispatch(EVENT_DIALOG_CLOSED, {
          cartSize,
        });
      },
      (e) => {
        console.log('error getting cart size', e);
      }
    );
  };

  private getTipserSdkInstance(newProps: TipserContextData): TipserSDKInstance {
    if (!sdkInstance) {
      let tipserSdkOptions: TipserSdkConfig = omit(newProps, ['events, children']);

      if (newProps.lang) {
        tipserSdkOptions.defaultLang = newProps.lang;
      }

      sdkInstance = TipserSDK(newProps.posId, tipserSdkOptions);
      sdkInstance.addDialogClosedListener(this.onDialogClosed);
    }
    return sdkInstance;
  }

  private getTipserApiUrl(sdkConfig: TipserSdkConfig) {
    const apiHostnamePart = sdkConfig.env === 'localhost' ? 'dev' : sdkConfig.env.toLowerCase();
    return `https://t3-${apiHostnamePart}-api.tipser.com`;
  }

  private preparePropsForContext = (newProps: TipserContextData, config: TipserSdkConfig): TipserContextData => {
    const contextBase = {
      ...newProps,
      ...config,
    };
    setLang(contextBase.lang);
    contextPreparedData = {
      ...contextBase,
      tipserSdk: this.getTipserSdkInstance(contextBase),
      tipserApiUrl: this.getTipserApiUrl(contextBase),
    };
    return contextPreparedData;
  };

  componentDidUpdate(prevProps: TipserElementsProviderProps) {
    if (this.props !== prevProps) {
      this.updateContext(this.props.config);
    }
  }

  private updateContext = (value: TipserConfigData) => {
    const contextPreparedData = this.preparePropsForContext(this.state, value);
    this.setState(contextPreparedData);
    return contextPreparedData;
  };

  render() {
    const TContext = TipserContext();
    return (
      <TContext.Provider value={{ data: this.state, updateContext: this.updateContext }}>
        <ErrorPopup updateContext={this.updateContext} context={this.state} />
        {this.props.children}
      </TContext.Provider>
    );
  }
}

// annotation for react components to inject TipserContext into their props
export const connectedElement = <K extends Contextable>(Component: React.ComponentType<K>) => {
  type PropsWithoutContext = Exclude<K, keyof Contextable>;

  const WrapperComponent = class extends React.Component<PropsWithoutContext> {
    static displayName = `connectedElement(${Component.displayName || Component.name})`;
    render() {
      const TContext = TipserContext();
      return (
        <TContext.Consumer>
          {({ data, updateContext }) => {
            if (!updateContext || !data.env) {
              throw 'Tipser Context Consumer is used without Provider. Have you used <TipserElementsProvider> tag ?';
            }
            return <Component {...this.props} updateContext={updateContext} context={data} />;
          }}
        </TContext.Consumer>
      );
    }
  };
  hoistNonReactStatics(WrapperComponent, Component);
  return WrapperComponent;
};
