import React, { ComponentType } from 'react';
import ReactResizeDetector from 'react-resize-detector';

import { ComponentModel } from '../../common/models';
import { withContentful } from '../../connectors/with-contentful';
import { fetchTipserCollection } from '../../api/tipser-api';
import { connectedElement, Contextable } from '../../tipser-context';
import { TipserProductComponent } from '../product/tipser-product';
import { PRODUCT_ALTERNATIVE_RENDERER } from '../..';

import { TipserProductModel } from '../product/model';
import { customBemNamesFactory, BREAKPOINTS } from '../../common/utils';
import { Tracking, withInViewTracker, TrackableProps } from '../../common/analytics';
import { ImageSize } from '../product/product-card-renderer';
import { FadeAnimation } from '../fade-animation/fade-animation';

import './collection.less';
import { LoaderCenter } from '../../common/loader';

const bem = customBemNamesFactory('te-collection');
const bemClasses = {
  small: bem('small'),
  large: bem('large'),
  extraLarge: bem('extra-large'),
};

export const COMPONENT_COLLECTION_CONTENT_TYPE = 'collection';

export interface CollectionType {
  // model of collection from tipser.com
  id?: string;
  name?: string;
  created?: string;
  friendlyUrl?: string;
  items?: {
    // items are not products yet
    id?: string;
    product?: TipserProductModel; // here we have products
  }[];
}

export interface CollectionModel extends Partial<CollectionDefaultProps>, ComponentModel {
  products?: TipserProductModel[];
  collectionId?: string;
  collection?: CollectionType;
  title?: string;
  name?: string;
  posId?: string;
}

interface CollectionProps extends TrackableProps, CollectionModel, Contextable {}

interface CollectionDefaultProps {
  imgSize: ImageSize;
}

export interface CollectionComponentState {
  collection?: CollectionType;
  loading: boolean;
}

class CollectionNotConnected extends React.Component<CollectionProps, CollectionComponentState> {
  static defaultProps: CollectionDefaultProps = {
    imgSize: '1.0',
  };
  state = {
    collection: null,
    loading: false,
  };

  componentDidMount() {
    if (this.props.collectionId) {
      this.loadCollectionData(this.props.collectionId);
    } else if (this.props.products) {
      this.loadCollectionFromProductList(this.props.products);
    }
  }

  componentDidUpdate(prevProps) {
    if (this.props.collectionId && prevProps.collectionId !== this.props.collectionId) {
      this.setState({
        loading: true,
      });
      this.loadCollectionData(this.props.collectionId);
    } else if (this.props.products && prevProps.products !== this.props.products) {
      this.loadCollectionFromProductList(this.props.products);
    }
  }

  loadCollectionFromProductList(products: TipserProductModel[]) {
    const items = products.map((item: TipserProductModel) => ({ product: item }));
    this.setData({
      items,
    });
  }

  loadCollectionData(collectionId) {
    fetchTipserCollection(collectionId).then((response) => {
      this.setData(response.data);
    });
  }

  setData(collection: CollectionType) {
    this.setState({ collection, loading: false });
    if (this.props.setTrackingData) {
      const { id = '(no id)', items } = collection;
      this.props.setTrackingData([{ id, items }]);
    }
  }

  getClassNameByWidth(width) {
    if (width <= BREAKPOINTS.SMALL) {
      return bemClasses.small;
    } else if (width > BREAKPOINTS.SMALL && width < BREAKPOINTS.LARGE) {
      return bemClasses.large;
    } else {
      return bemClasses.extraLarge;
    }
  }

  onProductClick = (product) => Tracking.dispatchClickProductInCollection(product);

  render() {
    const { collection } = this.state;

    return (
      <div className="te-collection-container">
        {collection && (
          <ReactResizeDetector handleWidth>
            {({ width }) => (
              <FadeAnimation
                className={`te-collection ${this.getClassNameByWidth(width)}`}
                fadeIn={!this.state.loading}
                fadeOut={this.state.loading}
              >
                {collection.items &&
                  collection.items.map((item, index) => (
                    <TipserProductComponent
                      key={index}
                      dontLoadProduct={true}
                      product={item.product}
                      productId={item.product.id}
                      renderer={PRODUCT_ALTERNATIVE_RENDERER.CARD}
                      imgSize={this.props.imgSize}
                      onProductClick={this.onProductClick}
                    />
                  ))}
              </FadeAnimation>
            )}
          </ReactResizeDetector>
        )}
        {(!collection || this.state.loading) && <LoaderCenter delayed />}
      </div>
    );
  }
}

export const Collection: ComponentType<CollectionProps> = connectedElement(CollectionNotConnected);

export const TrackableCollection: ComponentType<CollectionModel> = withInViewTracker({
  trackingCallback: (data) => data && Tracking.dispatchCollectionView(data),
})(Collection);

export const CmsCollection: ComponentType<CollectionModel & TrackableProps> = withContentful()(Collection);
