import React, { ComponentType } from 'react';
import { InView } from 'react-intersection-observer';
import chunk from 'lodash.chunk';

import { PageModel } from '../..';
import { cappedImageUrl } from '../../common/images';
import { Link } from '../..';
import { withContentful } from '../../connectors/with-contentful';
import { ComponentModel } from '../../common/models';
import { Any } from '../any';
import { markdown } from '../../common/markdown';
import { t } from '../../i18n';
import { ImageFadeIn } from '../../common/image-fade-in';
import { DelayedInView } from './delayed-in-view';
import { CustomLazyImage } from './lazy-load-img';

import './related-pages.less';

export const COMPONENT_RELATED_PAGES_CONTENT_TYPE = 'relatedPages';
/**
 * Number of pages wrapped by the same IntersectionObserver. Used for CPU optimization.
 */
const PAGES_GROUP_BY_COUNT = 4;

export interface RelatedPagesModel extends ComponentModel {
  pages: PageModel[];
  background?: string;
  useHeadline?: boolean;
  headline?: string;
}

/**
 * For case of many RelatedPagesComponent on one page, performance optimization of InView is done.
 *
 * Optimizations:
 * - one InView per RelatedPagesComponent, that delays initialization of InView for its pages images
 * - one InView per number of PAGES_GROUP_BY_COUNT pages, instead of one InView per page
 */
export class RelatedPagesComponent extends React.Component<RelatedPagesModel> {
  memoizedGroupedPages = undefined;

  get groupedPages() {
    if (this.props.pages.length === 0) {
      return [];
    }

    if (!this.memoizedGroupedPages) {
      this.memoizedGroupedPages = chunk(this.props.pages, PAGES_GROUP_BY_COUNT);
    }

    return this.memoizedGroupedPages;
  }

  render() {
    const { background, useHeadline, headline } = this.props;

    const style = background ? { background } : {};

    return (
      <InView>
        {({ inView, ref }) => (
          <div className="te-related-pages" style={style} ref={ref}>
            <div className="te-related-pages-header">
              <h2>{useHeadline ? headline : t('more_to_read')}</h2>
            </div>
            {this.groupedPages.map((pages) => (
              <PagesGroup pages={pages} key={pages[0].id} maybeInView={inView} />
            ))}
          </div>
        )}
      </InView>
    );
  }
}

interface PagesProps {
  pages: PageModel[];
  maybeInView: boolean;
}

/**
 * Displays group of Pages
 *
 * A check whether the group is in view is delayed until `maybeInView` prop is true.
 * The check delay is done from performance reasons.
 */
class PagesGroup extends React.Component<PagesProps> {
  render() {
    const { maybeInView, pages } = this.props;

    return (
      <DelayedInView isOn={maybeInView} triggerOnce={true}>
        {({ inView, ref }) => (
          <div className="te-related-pages-box-container" ref={ref}>
            {pages.map((page) => (
              <Page page={page} key={page.id} inView={inView} />
            ))}
          </div>
        )}
      </DelayedInView>
    );
  }
}

/**
 * Displays Page
 *
 * Uses CustomLazyImage to load an image
 */
function Page({ page, inView }: { page: PageModel; inView: boolean }) {
  return (
    <Any
      key={page.id}
      {...page}
      render={(item: PageModel) => (
        <div className="te-related-page-box">
          <Link page={item} {...item}>
            <div className="te-related-page-img-wrapper">
              <CustomLazyImage shouldLoad={inView} src={getImgUrl(page)} ImageTag={ImageFadeIn} />
            </div>
            <div className="te-related-page-title te-primary-color">{item.title}</div>
            {item.shortDescription && (
              <div
                className="te-related-page-description"
                dangerouslySetInnerHTML={{
                  __html: markdown(item.shortDescription),
                }}
              />
            )}
          </Link>
        </div>
      )}
    />
  );
}

function getImgUrl(page: PageModel) {
  const url = page.mainImage ? page.mainImage.file.url : null;
  return cappedImageUrl(url, 500);
}

export const RelatedPages: ComponentType<RelatedPagesModel> = withContentful()(RelatedPagesComponent);
