import React from 'react';

import { COMPONENT_IMAGE_CONTENT_TYPE, ImageComponent } from '..';
import { COMPONENT_CONTAINER_CONTENT_TYPE, ContainerComponent } from '..';
import { ComponentModel } from './models';
import { COMPONENT_GRID_CONTENT_TYPE, GridComponent } from '..';
import { COMPONENT_LAYOUT_COLUMN_CONTENT_TYPE, LayoutColumnComponent } from '../components/layouts/layout-column';
import { COMPONENT_PROFILE_CONTENT_TYPE, ProfileComponent } from '..';
import { COMPONENT_SLOT_CONTENT_TYPE, SlotComponent } from '..';
import { COMPONENT_SLOTTABLE_LINK_CONTENT_TYPE, SlottableLinkComponent } from '..';
import { COMPONENT_LINK_CONTENT_TYPE, LinkComponent } from '..';
import { COMPONENT_MENU_CONTENT_TYPE, MenuComponent } from '..';
import {
  COMPONENT_TIPSER_COLLECTION_CONTENT_TYPE,
  TipserCollectionComponent,
} from '../components/collection/tipser-collection';
import { COMPONENT_COLLECTION_CONTENT_TYPE, Collection } from '../components/collection/collection';
import { COMPONENT_PRODUCT_CONTENT_TYPE, Product } from '../components/product/product';
import { COMPONENT_HORIZONTAL_MENU_CONTENT_TYPE, HorizontalMenuComponent } from '..';
import { AnyComponent } from '../components/any';
import { COMPONENT_SITE_CONTENT_TYPE, SiteComponent } from '..';
import { COMPONENT_ENTRY_LINK_CONTENT_TYPE } from './models';
import { COMPONENT_PAGE_CONTENT_TYPE, PageComponent } from '..';
import { RelatedPagesComponent, COMPONENT_RELATED_PAGES_CONTENT_TYPE } from '..';
import {
  COMPONENT_DYNAMIC_RELATED_PAGES_CONTENT_TYPE,
  DynamicRelatedPagesComponent,
} from '../components/dynamic-related-pages/dynamic-related-pages';
import { InlineBrandComponent, COMPONENT_INLINE_BRAND_CONTENT_TYPE } from '../components/brand/inline-brand';
import { BrandHeroComponent, COMPONENT_BRAND_HERO_CONTENT_TYPE } from '../components/brand/brand-hero';
import { COMPONENT_HERO_CONTENT_TYPE, HeroContentComponent } from '../components/hero-content/hero-content';
import { Store, COMPONENT_STORE_CONTENT_TYPE } from '..';
import { COMPONENT_ARTICLE_CONTENT_TYPE, ArticleComponent } from '..';
import { COMPONENT_RICH_ARTICLE_CONTENT_TYPE, RichArticleComponent } from '../components/rich-article/rich-article';
import { LinkButton, COMPONENT_LINK_BUTTON_CONTENT_TYPE } from '../components/link-button/link-button';

function getComponent(contentType: string): React.ComponentType | null {
  switch (contentType) {
    case COMPONENT_HERO_CONTENT_TYPE:
      return HeroContentComponent;
    case COMPONENT_ARTICLE_CONTENT_TYPE:
      return ArticleComponent;
    case COMPONENT_RICH_ARTICLE_CONTENT_TYPE:
      return RichArticleComponent;
    case COMPONENT_CONTAINER_CONTENT_TYPE:
      return ContainerComponent;
    case COMPONENT_IMAGE_CONTENT_TYPE:
      return ImageComponent;
    case COMPONENT_GRID_CONTENT_TYPE:
      return GridComponent;
    case COMPONENT_LAYOUT_COLUMN_CONTENT_TYPE:
      return LayoutColumnComponent;
    case COMPONENT_PROFILE_CONTENT_TYPE:
      return ProfileComponent;
    case COMPONENT_INLINE_BRAND_CONTENT_TYPE:
      return InlineBrandComponent;
    case COMPONENT_BRAND_HERO_CONTENT_TYPE:
      return BrandHeroComponent;
    case COMPONENT_SLOT_CONTENT_TYPE:
      return SlotComponent;
    case COMPONENT_SLOTTABLE_LINK_CONTENT_TYPE:
      return SlottableLinkComponent;
    case COMPONENT_LINK_CONTENT_TYPE:
      return LinkComponent;
    case COMPONENT_MENU_CONTENT_TYPE:
      return MenuComponent;
    case COMPONENT_HORIZONTAL_MENU_CONTENT_TYPE:
      return HorizontalMenuComponent;
    case COMPONENT_TIPSER_COLLECTION_CONTENT_TYPE:
      return TipserCollectionComponent;
    case COMPONENT_COLLECTION_CONTENT_TYPE:
      return Collection;
    case COMPONENT_STORE_CONTENT_TYPE:
      return Store;
    case COMPONENT_PRODUCT_CONTENT_TYPE:
      return Product;
    case COMPONENT_ENTRY_LINK_CONTENT_TYPE:
      return AnyComponent;
    case COMPONENT_SITE_CONTENT_TYPE:
      return SiteComponent;
    case COMPONENT_PAGE_CONTENT_TYPE:
      return PageComponent;
    case COMPONENT_RELATED_PAGES_CONTENT_TYPE:
      return RelatedPagesComponent;
    case COMPONENT_DYNAMIC_RELATED_PAGES_CONTENT_TYPE:
      return DynamicRelatedPagesComponent;
    case COMPONENT_LINK_BUTTON_CONTENT_TYPE:
      return LinkButton;
    default:
      return null;
  }
}

const getErrorMessage = (contentType) => `
  There is no component for contentType='${contentType}'.
`;

export const contentTypeMapper = (component: ComponentModel, additionalProps?: any) => {
  const props = { ...component, ...additionalProps };
  const Component = getComponent(props.contentType);
  if (Component) {
    return <Component {...props} />;
  }
  console.error(new Error(getErrorMessage(props.contentType)));
  return null;
};
