import { useState, useContext, useEffect, useMemo } from 'react';
import * as contentful from 'contentful';
import { TipserContext, TipserContextData } from '../../tipser-context';

// singleton holder
let contentfulClientInstance: contentful.ContentfulClientApi;

function getAllContentfulProperties(config) {
  return {
    ...config.cms,
    space: config.contentfulSpace,
    accessToken: config.contentfulApiKey,
    environment: config.contentfulEnv,
  };
}

// initialize contentful client
export const contentfulClient = (config?: TipserContextData): contentful.ContentfulClientApi => {
  !contentfulClientInstance && (contentfulClientInstance = contentful.createClient(getAllContentfulProperties(config)));
  return contentfulClientInstance;
};

export function useContentfulClient(): { client?: contentful.ContentfulClientApi } {
  const [client, setClient] = useState(undefined);
  const context = useContext(TipserContext());
  const contentfulConfigValues = useMemo(() => Object.values(getAllContentfulProperties(context.data)), [context.data]);

  useEffect(() => {
    if (context && !client) {
      setClient(contentfulClient(context.data));
    }
  }, [...contentfulConfigValues, setClient]);

  return {
    client,
  };
}
