import React, { ComponentType, ReactNode } from 'react';

import { ComponentModel } from '../common/models';
import { withContentful } from '../connectors/with-contentful';
import { contentTypeMapper } from '../common/content-type-mapper';

interface AnyComponentProps<T> extends ComponentModel {
  render?: (props: T) => ReactNode;
}

export class AnyComponent<T extends AnyComponentProps<T>> extends React.Component<T> {
  render() {
    const props = this.props;
    //TODO: is the hack with props.$$typeof still needed?!
    return (
      <>
        {!(props as any).$$typeof && this.props.render && this.props.render(this.props)}
        {!(props as any).$$typeof && !this.props.render && contentTypeMapper({ ...props })}
      </>
    );
  }
}

export const Any: ComponentType<any> = withContentful()(AnyComponent);
