import { connect } from 'react-refetch';

import { COMPONENT_PAGE_CONTENT_TYPE } from '../..';
import { ContentfulConnectorComponent } from './contentful-connector-component';
import { contentfulClient } from './contentful-client';
import { checkContentfulStructure, currentProcessing, INCLUDES_IN_CONTENTFUL } from './contentful-parser';
import { toContentfulCmsLocale } from '../../common/utils';

export const ContentfulSlugConnector = connect((props: any) => ({
  data: {
    comparison: props.slug + props.context.lang, // reload if
    value: () => {
      if (currentProcessing.contentfulStructure[props.slug] && currentProcessing.currentLocale === props.context.lang) {
        const data = checkContentfulStructure(props.slug, props);
        return data;
      } else {
        currentProcessing.contentfulStructure = {};
        currentProcessing.parsed = {};
      }
      currentProcessing.currentLocale = props.context.lang;
      const v = contentfulClient(props.context).getEntries({
        'fields.slug': props.slug,
        content_type: COMPONENT_PAGE_CONTENT_TYPE,
        locale: toContentfulCmsLocale(props.context.lang),
        include: INCLUDES_IN_CONTENTFUL,
      });
      return v;
    },
  },
}))(ContentfulConnectorComponent);
