import { useContentfulClient } from '../../connectors/contentful/contentful-client';
import { useCallback } from 'react';
import { INCLUDES_IN_CONTENTFUL } from '../../connectors/contentful/contentful-parser';
import get from 'lodash/get';

export function usePosSubPageIdGetter(): {
  getPosPageId: (posId: string, siteName: string) => Promise<string | undefined> | undefined;
} {
  const { client } = useContentfulClient();
  const callback = useCallback(
    (posId, siteName) => {
      if (!client) {
        return Promise.resolve(undefined);
      }
      return getByPosIdSiteNameAndClient(posId, siteName, client);
    },
    [client]
  );

  return {
    getPosPageId: callback,
  };
}

async function getByPosIdSiteNameAndClient(posId, siteName, client): Promise<string | undefined> {
  if (!client) {
    return undefined;
  }

  try {
    const mainEntry = await client.getEntries({
      include: INCLUDES_IN_CONTENTFUL,
      'fields.posId': posId,
      content_type: 'pos',
    });
    const targetEntryId: unknown = get(mainEntry, `items[0].fields.${siteName}.sys.id`);
    if (typeof targetEntryId === 'string' && !!targetEntryId) {
      return targetEntryId;
    }
    return undefined;
  } catch (e) {
    console.error(e);
    return undefined;
  }
}
