import React from 'react';
import { ImageValidator } from '../../common/image-validator';

interface CustomLazyImageProps extends React.ImgHTMLAttributes<any> {
  shouldLoad: boolean;
  ImageTag: React.ComponentType;
}

/**
 * Lazy loaded Image, but whether it should be loaded or not comes from props, not internal logic.
 *
 * The difference between this component and conditionally rendered <img> is
 * the component still displays an image if it was already loaded
 */
export class CustomLazyImage extends React.Component<CustomLazyImageProps, { isAlreadyLoaded: boolean }> {
  state = {
    isAlreadyLoaded: false,
  };

  onImgValidatorChange = () => {
    this.setState({ isAlreadyLoaded: true });
  };

  render() {
    const { shouldLoad, ImageTag, ...restOfProps } = this.props;
    const dontDisplayImage = !shouldLoad && !this.state.isAlreadyLoaded;

    if (dontDisplayImage) {
      return null;
    }

    return (
      <ImageValidator src={restOfProps.src} onChange={this.onImgValidatorChange}>
        {({ valid }) => valid === true && <ImageTag {...restOfProps} />}
      </ImageValidator>
    );
  }
}
