import React from 'react';

import ButtonClear from '../button-clear/button-clear';

import './modal.less';

interface ModalProps {
  onClose?: () => void;
}

export class Modal extends React.Component<ModalProps, any> {
  clickEventListener;

  componentDidMount() {
    this.clickEventListener = document.body.addEventListener('keydown', this.onKeyDown);
  }

  componentWillUnmount() {
    document.body.removeEventListener('keydown', this.onKeyDown);
  }

  onKeyDown = (e) => {
    const { onClose } = this.props;
    const isEscapeKey = e.key === 'Escape' || e.key === 'Esc'; //'Esc' happens on IE11
    if (isEscapeKey && onClose) {
      onClose();
    }
  };

  render() {
    const { children } = this.props;
    return (
      <div className="te-product-overlay">
        <div className="te-product-card-modal">
          <ButtonClear
            className="tipser-icon tipser-icon-cross tipser-icon-large te-product-modal-close"
            onClick={this.props.onClose}
          />
          <div className="te-product-card-modal-inner">{children}</div>
        </div>
      </div>
    );
  }
}
