import React, { ComponentType } from 'react';
import './menu.less';
import { contentTypeMapper } from '../../common/content-type-mapper';
import { SlotModel } from './slot';
import { ComponentModel } from '../../common/models';
import { withContentful } from '../../connectors/with-contentful';
import { connectedElement, Contextable } from '../../tipser-context';
import { shallowObjectCompare } from '../../common/utils';
import { EVENT_AFTER_SLOT_CONTENT_CHANGE, EVENT_SLOT_CONTENT_CHANGE } from '../../tipser-events';

export const COMPONENT_SLOTTABLE_LINK_CONTENT_TYPE = 'slottableLink';

export interface SlottableLinkModel extends ComponentModel {
  text?: string;
  slot?: SlotModel;
  sourceComponent?: ComponentModel;
  targetComponent?: ComponentModel;
  params?: any;
}

interface SlottableLinkComponentProps {
  updateClickedItem?: () => void;
  clearClickedItem?: () => void;
  isInGrid?: boolean; //FIXME: it is obscure to pass this through
}

type SlottableLinkProps = SlottableLinkModel & SlottableLinkComponentProps & Contextable;

class SlottableLinkComponentNotConnected extends React.Component<SlottableLinkProps, any> {
  constructor(props) {
    super(props);
    this.props.context.events.register(EVENT_AFTER_SLOT_CONTENT_CHANGE, this.onSlotChanged.bind(this));
  }

  onSlotChanged(params) {
    const { slot, clearClickedItem, updateClickedItem, targetComponent } = this.props;
    if (!params.targetComponent) {
      clearClickedItem && clearClickedItem();
    } else if (
      params.slotId === slot.id &&
      shallowObjectCompare(this.props.params, params.params) &&
      targetComponent &&
      targetComponent.id === params.targetComponent.id
    ) {
      updateClickedItem && updateClickedItem();
    }
  }

  onClick() {
    this.props.context.events.dispatch(EVENT_SLOT_CONTENT_CHANGE, {
      slotId: this.props.slot.id,
      targetComponent: this.props.targetComponent,
      params: this.props.params,
    });
  }

  render() {
    const { sourceComponent, text, isInGrid } = this.props;
    return (
      <span onClick={() => this.onClick()}>
        {sourceComponent ? contentTypeMapper(Object.assign({ isInGrid: isInGrid }, sourceComponent)) : text}
      </span>
    );
  }
}

export const SlottableLinkComponent: ComponentType<SlottableLinkModel> = connectedElement(
  SlottableLinkComponentNotConnected
);

export const SlottableLink: ComponentType<SlottableLinkModel> = withContentful()(SlottableLinkComponent);
