import axios from 'axios';

import { TipserContextData, getTipserContextData } from '../tipser-context';
import { TipserProductModel } from '../components/product/model';
import { handleError } from '../tipser-error';
import { t } from '../i18n';

const SERVER_ERROR_MESSAGE = t('default_server_error');

axios.interceptors.response.use(
  (response) => response,
  (error) => {
    if (!error.response || (error.response && error.response.status >= 500)) {
      handleError({ message: SERVER_ERROR_MESSAGE });
    }
    return Promise.reject(error);
  }
);

export const fetchTipserProduct = (productId: string, posId: string): Promise<{ data: TipserProductModel }> => {
  const context: TipserContextData = getTipserContextData();
  const getProductUrl = `${context.tipserApiUrl}/v4/products/${productId}?pos=${posId}`;
  return axios.get(getProductUrl);
};

export const fetchTipserCollection = (collectionId: string) => {
  const context: TipserContextData = getTipserContextData();
  return axios.get(`${context.tipserApiUrl}/v4/collections/${collectionId}`);
};

export const fetchTipserBrandProducts = (brandName: string) => {
  const context: TipserContextData = getTipserContextData();
  return axios.get(`${context.tipserApiUrl}/v3/products?skip=0&limit=8&brands=${brandName}`);
};

export const fetchTipserShop = (posId: string) => {
  const context: TipserContextData = getTipserContextData();
  return axios.get(`${context.tipserApiUrl}/v4/shops/byId/${posId}`);
};
