import React, { ComponentType } from 'react';
import cx from 'classnames';

import { withContentful } from '../../connectors/with-contentful';
import { contentTypeMapper } from '../../common/content-type-mapper';
import { ComponentModel } from '../../common/models';

import './layout-column.less';

export const COMPONENT_LAYOUT_COLUMN_CONTENT_TYPE = 'layoutColumn';

export interface LayoutColumnModel extends ComponentModel {
  name?: string;
  headline?: string;
  verticalAlign?: string;
  width?: string;
  backgroundColor?: string;
  columnsRatios?: any; // TODO how lists are represented in contentful
  items: ComponentModel[];
}

export class LayoutColumnComponent extends React.Component<LayoutColumnModel, any> {
  render() {
    const { items, backgroundColor, width, verticalAlign } = this.props;
    const bgColor = backgroundColor ? backgroundColor : 'unset';
    const contentClassNames = cx('te-columns-wrapper', {
      'full-width': width === 'Full width',
      'medium-width': width === 'Extended width',
      'small-width': width === 'Article width',
    });
    const itemClassNames = cx('te-columns-item', {
      top: verticalAlign === 'top',
      center: verticalAlign === 'center',
      bottom: verticalAlign === 'bottom',
    });

    return (
      <div className="te-columns" style={{ backgroundColor: bgColor }}>
        {this.props.headline && <h2>{this.props.headline}</h2>}
        <div className={contentClassNames}>
          {items &&
            items.map((item: ComponentModel, idx: number) => (
              <div key={`layout-item-${idx}`} className={itemClassNames}>
                {contentTypeMapper({ ...item })}
              </div>
            ))}
        </div>
      </div>
    );
  }
}

export const LayoutColumn: ComponentType<LayoutColumnModel> = withContentful()(LayoutColumnComponent);
