import { cappedImageUrl } from './images';
import marked from 'marked';

export const markdown = marked;

const renderer = new markdown.Renderer();

markdown.setOptions({
  renderer: renderer,
  headerIds: true,
});

renderer.image = (href: string) => {
  return `<figure class="imagewrapper">
        <picture>
            <source media="(max-width: 450px)" srcset="${cappedImageUrl(href, 450)}">
            <source media="(max-width: 959px)" srcset="${cappedImageUrl(href, 960)}">
            <source media="(min-width: 960px)" srcset="${cappedImageUrl(href, 1280)}">
            <img class="tipser-img" src="${cappedImageUrl(href, 450)}" alt="Image">
        </picture>
     </figure>`;
};

renderer.code = (code: string, language: string) => {
  if (language && language == 'attributes') {
    const renderHeading = (txt: string) => `<div class="method-list" >
            <h5 class="method-list-title" >
                ${txt}
            </h5>
        <ul class="method-list-group">`;

    const renderMethod = (body: string) => {
      const [title, ...rest] = body.split(/[\n]+/);
      const [name, hint] = title.split(':');
      return renderItem(name.trim(), hint, rest.join('\n'));
    };

    const renderItem = (name, hint, rest) => `<li class="method-list-item">
            <h3 class="method-list-item-label">${name}${
      hint ? `<span class="method-list-item-validation">${hint}</span>` : ''
    }</h3>
            <p class="method-list-item-description">
                ${rest}
            </p> 
        <li>`;

    return (
      code
        .split('---\n')
        .map((line) => {
          if (line.indexOf('#') > -1) {
            return renderHeading(line.replace('#', '').trim());
          } else {
            return renderMethod(line);
          }
        })
        .join('') + `</ul></div>`
    );
  } else {
    return `<pre>
            <code>
                ${code}
            </code>
        </pre>`;
  }
};
