import React, { ImgHTMLAttributes } from 'react';
import cx from 'classnames';

import { customBemNamesFactory } from '../utils';

import './image-fade-in.less';

const bem = customBemNamesFactory('te-image-fade-in');

interface State {
  loaded: boolean;
}

/**
 * Provides animation on image load. 100% compatible with <img> html element
 */

export class ImageFadeIn extends React.Component<ImgHTMLAttributes<HTMLImageElement>, State> {
  state = {
    loaded: false,
  };

  onLoad = (event) => {
    this.setState({ loaded: true });
    if (this.props.onLoad) {
      this.props.onLoad(event);
    }
  };

  render() {
    const { onLoad, className, ...restOfImgProps } = this.props;
    return (
      <img className={cx(className, bem({ loaded: this.state.loaded }))} {...restOfImgProps} onLoad={this.onLoad} />
    );
  }
}
