import React from 'react';
import cx from 'classnames';

import ButtonClear, { PropTypes as ButtonClearPropTypes } from '../button-clear/button-clear';

export interface PropTypes extends ButtonClearPropTypes {
  className?: string;
}

const ButtonCross: React.FunctionComponent<PropTypes> = ({ className, ...restOfProps }) => {
  const finalClassName = cx('te-button-cross tipser-icon tipser-icon-cross tipser-icon-large', className);
  return <ButtonClear className={finalClassName} {...restOfProps} />;
};

export default ButtonCross;
