import { contentfulClient } from './contentful-client';
import { toContentfulCmsLocale } from '../../common/utils';
import { EntryCollection } from 'contentful';

export const INCLUDES_IN_CONTENTFUL = 10;
const ENTRY = 'Entry';
const LINK_TYPE = 'Link';
const ENTRYLINK_TYPE = 'EntryLink';

// Object to keep already processed elements and locale
export let currentProcessing = {
  parsed: {},
  contentfulStructure: {},
  currentLocale: '',
};

export const checkContentfulStructure = (id, props) => {
  return new Promise((resolve) => {
    const structure = currentProcessing.contentfulStructure[id];

    // lets verify if any entry in the structure is link
    let isLink = false;
    Object.keys(structure).map((item) => {
      if (typeof structure[item] === 'object' && Array.isArray(structure[item])) {
        structure[item].map((item) => {
          isLink = isLink || isElementLink(item, props);
        });
      } else if (structure[item].sys && structure[item].sys.linkType == ENTRY) {
        isLink = true;
      }
    });

    if (isLink) {
      // retrive additional entries if they are links
      contentfulClient(props.context)
        .getEntries({
          'sys.id': id,
          locale: toContentfulCmsLocale(currentProcessing.currentLocale),
          include: INCLUDES_IN_CONTENTFUL,
        })
        .then((value: EntryCollection<any>) => {
          currentProcessing.parsed = {};
          const data = parseContentfulStructure({
            id: value.items[0].sys.id,
            contentType: value.items[0].sys.contentType.sys.id,
            ...value.items[0].fields,
          });
          currentProcessing.contentfulStructure[id] = data;

          resolve(data);
        });
    } else {
      // if entries are data (not links) simply resolve the promise
      resolve(currentProcessing.contentfulStructure[id]);
    }
  });
};

// checks if element is a link
export const isElementLink = (element, props) => {
  if (typeof element === 'object' && Array.isArray(element)) {
    return element.reduce((_acc, v) => {
      return isElementLink(v, props);
    }, false);
  } else if (element.sys && element.sys.linkType == ENTRY) {
    return true;
  }
  return false;
};

// contentful parser
export const parseContentfulStructure = (data) => {
  if (typeof data === 'object') {
    if (data.sys && data.fields) {
      const type = data.sys.contentType && data.sys.contentType.sys.id ? data.sys.contentType.sys.id : data.sys.type;
      if (data.sys.id && !currentProcessing.parsed[data.sys.id]) {
        // keep processing results
        let d = parseContentfulStructure({ id: data.sys.id, contentType: type, ...data.fields });
        currentProcessing.parsed[data.sys.id] = d;
        return d;
      } else if (currentProcessing.parsed[data.sys.id]) {
        // processing results already exists
        return currentProcessing.parsed[data.sys.id];
      } else if (data.sys.id) {
        return { id: data.sys.id, contentType: ENTRYLINK_TYPE, linkType: data.sys.linkType };
      }
    } else if (data.sys && data.sys.type == LINK_TYPE) {
      return { id: data.sys.id, contentType: ENTRYLINK_TYPE, linkType: data.sys.linkType };
    } else {
      let d: { id?: string } = Object.keys(data).reduce((acc, v) => {
        if (typeof data[v] === 'object' && !Array.isArray(data[v])) {
          if (data[v].sys && !data[v].fields && data[v].sys.type === LINK_TYPE) {
            acc[v] = { id: data[v].sys.id, linkType: data[v].sys.linkType, contentType: ENTRYLINK_TYPE };
          } else {
            acc[v] = parseContentfulStructure(data[v]);
          }
        } else {
          acc[v] = data[v];
        }
        return acc;
      }, {});

      if (d.id) currentProcessing.parsed[d.id] = d;

      let dd = Object.keys(data).reduce((acc, v) => {
        if (typeof data[v] === 'object' && Array.isArray(data[v])) {
          acc[v] = data[v].map((o) => {
            if (o.sys && currentProcessing.parsed[o.sys.id]) {
              return currentProcessing.parsed[o.sys.id];
            } else {
              return parseContentfulStructure(o);
            }
          });
        }
        return acc;
      }, {});

      currentProcessing.contentfulStructure[d.id] = { ...d, ...dd };
      return currentProcessing.contentfulStructure[d.id];
    }
  } else if (typeof data === 'object' && Array.isArray(data)) {
    let elem = data.map((o) => parseContentfulStructure(o));
    currentProcessing.contentfulStructure[elem.id] = elem;
    return elem;
  }

  // keep processing results
  currentProcessing.contentfulStructure[data.id] = data;
  return data;
};
