import contentful from 'contentful';
import { parseContentfulStructure, currentProcessing } from './contentful-parser';
import React from 'react';
import { LoaderCenter } from '../../common/loader';

// props for
export interface ContentfulConnectorComponentProps {
  id: string;
  children: any;
  data?: {
    value: { id: string; items: any[] };
    fulfilled?: boolean;
    pending?: boolean;
  };
}

// internal connector component, to be used without data fetching
export const ContentfulConnectorComponent = (props: ContentfulConnectorComponentProps) => {
  if (props.data && props.data.fulfilled) {
    //if data is loaded

    // short returns if the element is already parsed (or this is our structure)
    if (!props.data.value) {
      return props.children();
    }

    if (props.data.value.id) {
      return props.children(props.data.value);
    }
    if (!props.data.value.items) {
      return props.children(props.data.value);
    }

    const value: contentful.Entry<any> = props.data.value.items[0];

    if (!value) {
      return props.children();
    }

    // data parsing
    const parsedData = {
      ...parseContentfulStructure({
        id: value.sys.id,
        contentType: value.sys.contentType.sys.id,
        ...value.fields,
      }),
    };

    // save element parsed to cache by id
    currentProcessing.contentfulStructure[value.sys.id] = parsedData;

    // save element parsed to cache by slug
    if (value.fields.slug) {
      currentProcessing.contentfulStructure[value.fields.slug] = parsedData;
    }

    return props.children(parsedData);
  } else if (props.data && props.data.pending) {
    // if it is still loading
    return <LoaderCenter delayed />;
  }

  // TODO : just return nothing if data is not loading either not loaded yet, maybe error ?
  return props.children();
};
