import React, { ComponentType } from 'react';
import cx from 'classnames';

import { contentTypeMapper } from '../../common/content-type-mapper';
import { ComponentModel } from '../../common/models';
import { withContentful } from '../../connectors/with-contentful';

import './grid.less';

export const COMPONENT_GRID_CONTENT_TYPE = 'layoutGrid';

export interface GridModel extends ComponentModel {
  gridTemplate?: string;
  cssClass?: string;
  cssTheme?: string;
  gutter?: number; // horizontal spacing
  bottomMargin?: number;
  items: ComponentModel[];
}

export class GridComponent extends React.Component<GridModel, any> {
  render() {
    const { items, gridTemplate, cssClass, cssTheme } = this.props;
    const theme = cssTheme ? cssTheme : 'Default';

    return (
      <div
        className={cx('te-layout-grid', cssClass, {
          'fancy-grid': gridTemplate === 'Fancy Grid',
          'gaudy-grid': gridTemplate === 'Gaudy Grid',
          gaudy: theme === 'Modern',
          traditional: theme === 'Default',
        })}
      >
        {items &&
          items.map((item: ComponentModel, idx: number) => {
            return (
              <div key={`layout-item-${idx}`} className="te-container-item">
                {contentTypeMapper({ ...item }, { isInGrid: true })}
              </div>
            );
          })}
      </div>
    );
  }
}

export const Grid: ComponentType<GridModel> = withContentful()(GridComponent);
