import React from 'react';
import hoistNonReactStatics from 'hoist-non-react-statics';

import { Contextable, TipserContext } from '../tipser-context';
import { ContentfulSlugConnector } from './contentful/contentful-slug-connector';
import { ContentfulConnector } from './contentful/contentful-connector';
import { ComponentModel } from '../common/models';
import { getDisplayName } from '../common/utils';

export enum CONNECTOR_TYPE {
  CONTENTFUL = 'contentful',
  CONTENTFUL_BY_SLUG = 'contentful-by-slug',
}

const getContentfulConnector = (connectorType: CONNECTOR_TYPE) => {
  switch (connectorType) {
    case CONNECTOR_TYPE.CONTENTFUL_BY_SLUG:
      return ContentfulSlugConnector;
    case CONNECTOR_TYPE.CONTENTFUL:
      return ContentfulConnector;
    default:
      return ContentfulConnector;
  }
};

export function withContentful(connectorType?: CONNECTOR_TYPE) {
  return function<T extends Contextable & ComponentModel>(Component: React.ComponentType<T>) {
    const ComponentWithContentful = class<K extends ComponentModel> extends React.Component<K> {
      static displayName = `WithContentful(${getDisplayName(Component)})`;
      render() {
        const Connector = getContentfulConnector(connectorType);
        const TContext = TipserContext();
        return (
          <TContext.Consumer>
            {({ data, updateContext }) => (
              <Connector context={data} {...this.props}>
                {(connectorData) => (
                  <Component updateContext={updateContext} context={data} {...this.props} {...connectorData} />
                )}
              </Connector>
            )}
          </TContext.Consumer>
        );
      }
    };
    hoistNonReactStatics(ComponentWithContentful, Component);
    return ComponentWithContentful;
  };
}
