import React, { useState, useEffect } from 'react';

import axios from 'axios';

interface HeaderRendererProps {
  posId?: string;
}
const fetchLogo = (posId: string) =>
  axios
    .get(`https://t3-prod-api.tipser.com/v3/profile/byId/${posId}`)
    .then((response) => response.data.profilePictureUrl['250x']);

const useLogo = (posId: string) => {
  const [logo, setLogo] = useState(null);
  useEffect(() => {
    fetchLogo(posId).then(setLogo);
  }, [posId]);
  return logo;
};
export function Header({ posId }: HeaderRendererProps) {
  const logo = useLogo(posId);

  return (
    <div className="te-header">
      <div className="te-header-logo">
        <img src={logo} alt="" />
      </div>
    </div>
  );
}
export default Header;
