import React, { ComponentType } from 'react';
import { withContentful } from '../../connectors/with-contentful';
import { TipserContextData, Contextable, connectedElement } from '../../tipser-context';
import { PageModel } from '../page/page';
import { MenuModel } from '../navigation/menu';

export const COMPONENT_SITE_CONTENT_TYPE = 'siteStructure';

const DEFAULT_IE_FIX_ELEMENT_SELECTOR = 'body';

export interface SiteProps {
  id?: string;
  ieFixElementSelector?: string; // dom selector where additional css class for IE will be added
  options?: TipserContextData;
  lang?: string;
}

export interface SiteModel {
  id?: string;
  rootPage?: PageModel;
  mainMenu?: MenuModel;
  name?: string;
}

type SiteComponentProps = SiteModel & Contextable & SiteProps;

class SiteComponentNotConnected extends React.Component<SiteComponentProps, any> {
  componentDidMount() {
    var ua = window.navigator.userAgent; //Check the userAgent property of the window.navigator object
    var msie = ua.indexOf('MSIE '); // IE 10 or older
    var trident = ua.indexOf('Trident/'); //IE 11

    if (msie > 0 || trident > 0) {
      document
        .querySelector(
          this.props.ieFixElementSelector ? this.props.ieFixElementSelector : DEFAULT_IE_FIX_ELEMENT_SELECTOR
        )
        .classList.add('tipser-ie');
    }

    this.props.updateContext({
      site: {
        rootPage: this.props.rootPage,
        mainMenu: this.props.mainMenu,
        name: this.props.name,
        id: this.props.id,
      },
    });
  }

  render() {
    const { children } = this.props;
    return <div className="te-site">{children}</div>;
  }
}

export const SiteComponent: ComponentType<SiteProps> = connectedElement(SiteComponentNotConnected);

export const Site: ComponentType<SiteProps> = withContentful()(SiteComponent);
