import React, { ComponentType } from 'react';
import marked from 'marked';

import { cappedImageUrl } from '../../common/images';
import { BrandModel } from './brand';
import { withContentful } from '../../connectors/with-contentful';
import { Any } from '../any';
import { ComponentModel } from '../../common/models';

import './brand.less';
import { BACKGROUND_POSITION_OPTIONS } from '../../common/utils';

export const COMPONENT_BRAND_HERO_CONTENT_TYPE = 'brandHero';

export interface BrandHeroModel extends ComponentModel {
  title?: string;
  brand?: BrandModel;
  backgroundPosition?: BACKGROUND_POSITION_OPTIONS;
}

interface BrandHeroProps extends BrandHeroModel {
  headerColor?: string;
}

export class BrandHeroComponent extends React.Component<BrandHeroProps> {
  static defaultProps = {
    backgroundPosition: BACKGROUND_POSITION_OPTIONS.CENTER,
  };

  render() {
    const { brand, backgroundPosition } = this.props;
    const headerBackgroundImageUrl = brand.headerBackgroundImage
      ? 'url(' + cappedImageUrl(brand.headerBackgroundImage.file.url) + ')'
      : 'none';
    const style = {
      backgroundImage: headerBackgroundImageUrl,
      backgroundColor: brand.headerColor,
      backgroundPosition,
    };
    return (
      <Any
        {...brand}
        render={(brand: BrandModel) => (
          <React.Fragment>
            <div className="te-hero-brand">
              <div className="te-hero-brand-img" style={style} />
              <h1 className="te-hero-brand-name">{brand.brandName}</h1>
              <p className="te-hero-brand-title">{brand.title}</p>
            </div>
            <div className="te-brand">
              <figure className="te-brand-logo-intro">
                <div className={'te-brand-logo'}>
                  <img src={brand.logo.file.url} />
                </div>
                <figcaption className="te-brand-intro-text">
                  {brand.intro && <div dangerouslySetInnerHTML={{ __html: marked(brand.intro) }} />}
                </figcaption>
              </figure>
              <div className="te-brand-bio">
                {brand.content && <div dangerouslySetInnerHTML={{ __html: marked(brand.content) }} />}
              </div>
            </div>
          </React.Fragment>
        )}
      />
    );
  }
}

export const BrandHero: ComponentType<BrandHeroProps> = withContentful()(BrandHeroComponent);
