import React, { ComponentType } from 'react';
import cx from 'classnames';

import { ComponentModel } from '../../common/models';
import { withContentful } from '../../connectors/with-contentful';
import { contentTypeMapper } from '../../common/content-type-mapper';
import { Contextable, connectedElement } from '../../tipser-context';

import './container.less';

export enum COMPONENT_CONTAINER_TYPE {
  HORIZONTAL = 'horizontal',
  VERTICAL = 'vertical',
}

export const COMPONENT_CONTAINER_CONTENT_TYPE = 'container';

export interface ContainerModel extends ComponentModel {
  name?: string;
  type?: COMPONENT_CONTAINER_TYPE;
  items: ComponentModel[];
  cssClass?: string;
  isShop?: boolean;
}

type ContainerComponentProps = ContainerModel & Contextable;

class ContainerComponentNotConnected extends React.Component<ContainerComponentProps> {
  render() {
    const { items, type, cssClass } = this.props;

    const classes = cx(cssClass ? cssClass : '', 'te-container', {
      'te-container-horizontal': type === COMPONENT_CONTAINER_TYPE.HORIZONTAL,
      'te-container-vertical': type === COMPONENT_CONTAINER_TYPE.VERTICAL,
    });

    // untypical situation where from component props we add isShop property to context
    // It means that this container and all it subcomponents make a "tipser shop".
    // It is only relevant for analytics. We want to send analytics events only from
    // shops and not e.g. from blog posts
    return (
      <div className={classes}>
        {items &&
          items.map((item: ComponentModel, idx: number) => (
            <div key={`te-container-item-${idx}`} className="te-container-item">
              {contentTypeMapper({ ...item })}
            </div>
          ))}
      </div>
    );
  }
}

export const ContainerComponent: ComponentType<ContainerModel> = connectedElement(ContainerComponentNotConnected);

export const Container: ComponentType<ContainerModel> = withContentful()(ContainerComponent);
