import React, { ComponentType } from 'react';
import { withRouter } from 'react-router-dom';
import { NavLink } from 'react-router-dom';

import { AssetImageModel, ComponentModel } from '../../common/models';
import { withContentful } from '../../connectors/with-contentful';
import { MenuModel } from './menu';

import './menu.less';

export const COMPONENT_LINK_CONTENT_TYPE = 'link';

export interface LinkModel extends ComponentModel {
  text?: string;
  url?: string;
  page?: any;
  image?: AssetImageModel;
  submenu?: MenuModel;
  opensIn?: string;
  component?: ComponentModel;
}

interface RouterProps {
  location: any;
  history: any;
  match: any;
}

interface LinkProps extends LinkModel {
  onClick?: () => void;
}

class LinkComponentWithNoRouter extends React.Component<LinkProps & RouterProps> {
  isActive = (to) => (_match, location) => {
    if (to && to.length > 0 && to[0] !== '/') {
      return `${to}` === location.pathname;
    }
    return `${to}` === location.pathname;
  };

  getTo = () => {
    const { page, url, page: pageLink } = this.props;
    let to;
    if (pageLink) {
      to = pageLink.slug || pageLink.id;
    } else {
      if (page) {
        to = page.slug || page.id;
      } else {
        to = url;
      }
    }
    if (to && to.length > 0 && to[0] !== '/') {
      return '/' + to;
    }
    return to || '/';
  };

  clicked = () => {
    this.props.onClick && this.props.onClick();
  };

  render() {
    const { text, children } = this.props;
    const to = this.getTo();

    return (
      <NavLink to={to} className="te-link" isActive={this.isActive(to)} activeClassName="selected">
        <div onClick={this.clicked}>{children || text}</div>
      </NavLink>
    );
  }
}

export const LinkComponent: ComponentType<LinkProps> = withRouter(LinkComponentWithNoRouter);

export const Link: ComponentType<LinkProps> = withContentful()(LinkComponent);
