import React from 'react';
import '../common/tipser-element.less';
import { Any } from './any';
import { isIE } from '../common/browser-checks';

const DEFAULT_IE_FIX_ELEMENT_SELECTOR = 'body';

export interface TipserElementProps {
  id?: string;
  ieFixElementSelector?: string; // dom selector where additional css class for IE will be added
}

export class TipserElement extends React.Component<TipserElementProps, any> {
  componentDidMount() {
    if (isIE()) {
      document
        .querySelector(
          this.props.ieFixElementSelector ? this.props.ieFixElementSelector : DEFAULT_IE_FIX_ELEMENT_SELECTOR
        )
        .classList.add('tipser-ie');
    }
  }

  render() {
    const { id } = this.props;

    // class tipser-element used for custom POS styling do not change / remove it! Used in tipser-publishers
    return (
      <div className="tipser-element">
        <Any id={id} />
      </div>
    );
  }
}
