import React, { ComponentType } from 'react';
import './menu.less';
import { match } from 'react-router-dom';
import { ComponentModel } from '../../common/models';
import { withContentful } from '../../connectors/with-contentful';
import { contentTypeMapper } from '../../common/content-type-mapper';
import { connectedElement, Contextable } from '../../tipser-context';
import { EVENT_SLOT_CONTENT_CHANGE, EVENT_AFTER_SLOT_CONTENT_CHANGE } from '../../tipser-events';

export const COMPONENT_SLOT_CONTENT_TYPE = 'slot';

export interface SlotModel extends ComponentModel {
  mainContentComponents?: ComponentModel[];
  cssClass?: string;
  visible?: boolean;
}

interface SlotComponentProps extends SlotModel {
  history?: any;
  match?: match<{ component: string }>;
  location?: any;
  targetComponent?: ComponentModel;
  params?: any;
}

type SlotProps = SlotComponentProps & Contextable;

class SlotComponentNotConnected extends React.Component<SlotProps, any> {
  slotElement;

  constructor(props: SlotProps) {
    super(props);
    this.state = {
      visible: props.visible,
      currentTargetComponent: null,
    };
    this.props.context.events.register(EVENT_SLOT_CONTENT_CHANGE, this.onSlotChange.bind(this));
    this.slotElement = React.createRef();
  }

  onSlotChange(data) {
    if (data.slotId === this.props.id) {
      this.setCurrentComponent(data.targetComponent, data.params);
    }
  }

  setCurrentComponent(targetComponent, params) {
    this.setState(
      {
        currentTargetComponent: { ...targetComponent, ...params },
      },
      () => {
        this.props.context.events.dispatch(EVENT_AFTER_SLOT_CONTENT_CHANGE, {
          slotId: this.props.id,
          targetComponent: { ...targetComponent, ...params },
          params: params,
        });
      }
    );
  }

  onBackButtonClicked() {
    this.setState(
      {
        currentTargetComponent: null,
      },
      () => {
        this.props.context.events.dispatch(EVENT_AFTER_SLOT_CONTENT_CHANGE, {
          slotId: this.props.id,
          targetComponent: null,
          params: null,
        });
      }
    );
  }

  componentDidUpdate() {
    // TODO remove try/catch after making ssure the code works in all browsers

    try {
      const slotPos = this.slotElement.current.getBoundingClientRect().top;
      const bodyPos = document.body.getBoundingClientRect().top;
      const offset = slotPos - bodyPos;

      window.scroll({
        top: offset - 140,
        left: 0,
        behavior: 'smooth',
      });
    } catch (e) {}
  }

  render() {
    const { mainContentComponents, cssClass } = this.props;
    const currentTargetComponent = this.state.currentTargetComponent;
    const cssClasses = cssClass ? ' ' + cssClass : '';
    return (
      <div ref={this.slotElement}>
        {currentTargetComponent && (
          <span className={'te-back-button' + cssClasses} onClick={() => this.onBackButtonClicked()}>
            Back
          </span>
        )}
        {currentTargetComponent && contentTypeMapper(currentTargetComponent)}
        {!currentTargetComponent &&
          mainContentComponents.map((item, index) => <span key={index}>{contentTypeMapper({ ...item })}</span>)}
      </div>
    );
  }
}

export const SlotComponent: ComponentType<SlotModel> = connectedElement(SlotComponentNotConnected);

export const Slot: ComponentType<SlotModel> = withContentful()(SlotComponent);
