import React, { ComponentType } from 'react';
import cx from 'classnames';
import { ComponentModel, AssetImageModel } from '../../common/models';
import { withContentful } from '../../connectors/with-contentful';
import { cappedImageUrl, thumbImageUrl } from '../../common/images';
import { contentTypeMapper } from '../../common/content-type-mapper';
import { cssObjectToReactCss, BACKGROUND_POSITION_OPTIONS } from '../../common/utils';
import { ObjectFitImage } from '../../common/lazy-img';

import './hero-content.less';

export const COMPONENT_HERO_CONTENT_TYPE = 'heroContent';

export interface HeroContentModel extends ComponentModel {
  blurb?: ComponentModel;
  blurbPlacement?: string;
  backgroundImage?: AssetImageModel;
  backgroundColor?: string;
  blurbCss?: {};
  height?: string;
  backgroundPosition?: BACKGROUND_POSITION_OPTIONS;
}

export class HeroContentComponent extends React.Component<HeroContentModel, any> {
  static defaultProps = {
    height: 'tall',
    blurbPlacement: BACKGROUND_POSITION_OPTIONS.CENTER,
  };

  render() {
    const {
      blurb,
      backgroundPosition,
      blurbCss,
      height,
      blurbPlacement,
      backgroundImage,
      backgroundColor,
    } = this.props;
    const blurbStyle = blurbCss ? cssObjectToReactCss(blurbCss) : {};

    return (
      <div
        className={cx('te-hero-component', blurbPlacement.toLowerCase(), height.toLowerCase())}
        style={{ backgroundColor: backgroundColor }}
      >
        {this.props.blurb && (
          <div className="te-hero-blurb" style={blurbStyle}>
            {contentTypeMapper({ ...blurb }, { isInHeroContent: true })}
          </div>
        )}

        {backgroundImage && (
          <ObjectFitImage
            className="te-hero-component_background-image"
            backgroundPosition={backgroundPosition}
            fit="cover"
            src={cappedImageUrl(backgroundImage.file.url)}
            loadingPlaceholderSrc={thumbImageUrl(backgroundImage.file.url)}
          />
        )}
      </div>
    );
  }
}

export const Hero: ComponentType<HeroContentModel> = withContentful()(HeroContentComponent);
