import React, { ComponentType } from 'react';

import { markdown } from '../../common/markdown';
import { withContentful } from '../../connectors/with-contentful';
import { ComponentModel } from '../../common/models';

import './article.less';

export const COMPONENT_ARTICLE_CONTENT_TYPE = 'article';

export interface ArticleModel extends ComponentModel {
  content?: string;
}

export class ArticleComponent extends React.Component<ArticleModel> {
  render() {
    const content = this.props.content || '';
    return (
      <section className="te-article">
        <div
          className="te-article-content-container"
          dangerouslySetInnerHTML={{
            __html: markdown(content),
          }}
        />
      </section>
    );
  }
}

export const Article: ComponentType<ArticleModel> = withContentful()(ArticleComponent);
