import React, { ComponentType } from 'react';
import marked from 'marked';

import { cappedImageUrl } from '../../common/images';
import { RelatedPages } from '../..';
import { BrandModel } from './brand';
import { ComponentModel } from '../../common/models';
import { withContentful } from '../../connectors/with-contentful';
import { connectedElement, Contextable } from '../../tipser-context';
import { PRODUCT_ALTERNATIVE_RENDERER } from '../..';
import { t } from '../../i18n';
import { Any } from '../any';
import { CmsProduct } from '../..';

import './brand.less';
import './inline-brand.less';

export const COMPONENT_INLINE_BRAND_CONTENT_TYPE = 'layoutBrandInline';

export interface FeaturedProduct {
  productId: string;
  name: string;
}

export interface InlineBrandModel extends ComponentModel {
  title?: string;
  brand?: BrandModel;
  featuredProducts?: FeaturedProduct[];
}

export interface InlineBrandProps extends InlineBrandModel {
  headerColor?: string;
}

export type InlineBrandComponentProps = InlineBrandProps & Contextable;

class InlineBrandComponentNotConntected extends React.Component<InlineBrandComponentProps, any> {
  render() {
    const { featuredProducts, headerColor, brand } = this.props;
    const headerBackgroundImageUrl = brand.headerBackgroundImage
      ? 'url(' + cappedImageUrl(brand.headerBackgroundImage.file.url) + ')'
      : 'none';

    return (
      <Any
        {...brand}
        render={(brand: BrandModel) => (
          <div className="te-inline-brand">
            <div
              className="te-profile"
              style={{ backgroundImage: headerBackgroundImageUrl, backgroundColor: headerColor }}
            >
              <h1 className="te-profile-title">{brand.brandName}</h1>

              <figure className="te-brand-content">
                <figcaption>
                  {brand.intro && (
                    <div className="te-brand-content-intro" dangerouslySetInnerHTML={{ __html: marked(brand.intro) }} />
                  )}
                </figcaption>
                <div className={'te-brand-content-logo'}>
                  <img src={brand.logo.file.url} />
                </div>
              </figure>
            </div>

            <div className="te-inline-brand-content">
              {brand.content && (
                <div className="te-brand-bio" dangerouslySetInnerHTML={{ __html: marked(brand.content) }} />
              )}

              {brand.relatedPages && (
                <RelatedPages pages={brand.relatedPages} headline={t('related_posts')} useHeadline={true} />
              )}

              {featuredProducts && (
                <React.Fragment>
                  <h2 className="te-brand-subtitle">{t('featured_products')}</h2>
                  <div className="te-brand-products te-collection">
                    {featuredProducts.map((prod, index) => (
                      <CmsProduct
                        {...prod}
                        posId={this.props.context.posId}
                        renderer={PRODUCT_ALTERNATIVE_RENDERER.CARD}
                        key={index}
                      />
                    ))}
                  </div>
                </React.Fragment>
              )}
            </div>
          </div>
        )}
      />
    );
  }
}

export const InlineBrandComponent: ComponentType<InlineBrandModel> = connectedElement(
  InlineBrandComponentNotConntected
);

export const InlineBrand: ComponentType<InlineBrandModel> = withContentful()(InlineBrandComponent);
