import React, { ComponentType } from 'react';
import './page.less';
import { connectedElement, Contextable } from '../../tipser-context';
import { ComponentModel } from '../../common/models';
import { withContentful, CONNECTOR_TYPE } from '../../connectors/with-contentful';
import { contentTypeMapper } from '../../common/content-type-mapper';
import { ImageModel } from '../..';

export const COMPONENT_PAGE_CONTENT_TYPE = 'page';

export interface PageModel extends ComponentModel {
  posId?: string;
  slug?: string;
  title?: string;
  shortDescription?: string;
  relatedPages?: PageModel[];
  items?: ComponentModel[];
  mainImage?: ImageModel;
  backgroundColor?: string;
}

type PageComponentProps = PageModel & Contextable;

class PageComponentNotConnected extends React.Component<PageComponentProps> {
  render() {
    const { items, backgroundColor } = this.props;
    const style: React.CSSProperties = backgroundColor ? { backgroundColor } : null;
    return (
      <div className="te-page" style={style}>
        <article id="te-page-content-container">
          {items &&
            items.map((item: ComponentModel, index: number) => (
              <React.Fragment key={index}>{contentTypeMapper({ ...item })}</React.Fragment>
            ))}
        </article>
      </div>
    );
  }
}

export const PageComponent: ComponentType<PageModel> = connectedElement(PageComponentNotConnected);

export const Page: ComponentType<PageModel> = withContentful()(PageComponent);
export const PageBySlug: ComponentType<PageModel> = withContentful(CONNECTOR_TYPE.CONTENTFUL_BY_SLUG)(PageComponent);
