import React, { ComponentType } from 'react';
import cx from 'classnames';

import { Document } from '@contentful/rich-text-types';
import { documentToReactComponents } from '@contentful/rich-text-react-renderer';
import { withContentful } from '../../connectors/with-contentful';
import { ComponentModel } from '../../common/models';
import { contentTypeMapper } from '../../common/content-type-mapper';

import '../article/article.less';
import './rich-article.less';

export const COMPONENT_RICH_ARTICLE_CONTENT_TYPE = 'articleRich';

type TextAlignmentType = 'left' | 'center' | 'right';

export interface RichArticleModel extends ComponentModel {
  title?: string;
  richContent?: Document;
  textAlignment: TextAlignmentType;
}

const DEFAULT_TEXT_ALIGNMENT = 'center';

const RICH_TEXT_RENDERER_OPTIONS = {
  renderNode: {
    //[Piotr] Without this handler, images would be ignored, see: https://github.com/contentful/rich-text/issues/61
    'embedded-asset-block': (node) => <img className="rich-article-image" src={node.data.target.file.url} />,
    'embedded-entry-block': (node) => contentTypeMapper(node.data.target),
  },
};

export class RichArticleComponent extends React.Component<RichArticleModel, any> {
  render() {
    const textAlignment = this.props.textAlignment ? this.props.textAlignment : DEFAULT_TEXT_ALIGNMENT;
    const containerClassNames = cx('te-article-content-container', `justified-${textAlignment}`);

    return (
      <section className="te-article te-article-rich">
        <div className={containerClassNames}>
          {documentToReactComponents(this.props.richContent, RICH_TEXT_RENDERER_OPTIONS)}
        </div>
      </section>
    );
  }
}

export const RichArticle: ComponentType<RichArticleModel> = withContentful()(RichArticleComponent);
