import React, { useEffect, useState } from 'react';
import { Page } from '../..';
import { usePosSubPageIdGetter } from './use-pos-sub-page-id-getter';
import { makeCancelable, silenceCancelable } from '../../common/utils/cancellable-promise';
import { LoaderCenter } from '../../common/loader/';

interface Props {
  posId: string;
  pageName: string;
  posIdFallback?: string;
}

export const PosSubPage = React.memo(function PosSite({ posId, pageName, posIdFallback }: Props): React.ReactElement {
  const [state, setState] = useState({ loaded: false, pageId: undefined });
  const { getPosPageId } = usePosSubPageIdGetter();

  useEffect(() => {
    if (!getPosPageId) {
      return () => undefined;
    }

    const cancelable = makeCancelable(
      getPosPageId(posId, pageName).then((pageId) =>
        pageId || !posIdFallback ? pageId : getPosPageId(posIdFallback, pageName)
      )
    );

    silenceCancelable(cancelable.promise).then((pageId) => setState({ pageId, loaded: true }));

    return () => cancelable.cancel();
  }, [getPosPageId, pageName, posId]);

  if (!state.pageId && !state.loaded) {
    return <LoaderCenter delayed />;
  }

  if (!state.pageId && state.loaded) {
    return <div />; //todo think of proper 404 here (JIRA: https://tipser.atlassian.net/browse/T3-3744)
  }

  return <Page id={state.pageId} />;
});
