import { ContentfulConnectorComponent } from './contentful-connector-component';
import { connect } from 'react-refetch';
import { currentProcessing, INCLUDES_IN_CONTENTFUL, checkContentfulStructure } from './contentful-parser';
import { contentfulClient } from './contentful-client';
import { toContentfulCmsLocale } from '../../common/utils';

export const ContentfulConnector = connect((props: any) => ({
  data: {
    comparison: props.id + props.context.lang, // reload if
    value: () => {
      if (!props || !props.id) return Promise.resolve();
      if (currentProcessing.contentfulStructure[props.id] && currentProcessing.currentLocale === props.context.lang) {
        const data = checkContentfulStructure(props.id, props);
        return data;
      } else {
        currentProcessing.contentfulStructure = {};
        currentProcessing.parsed = {};
      }
      currentProcessing.currentLocale = props.context.lang;
      const v = contentfulClient(props.context).getEntries({
        'sys.id': props.id,
        locale: toContentfulCmsLocale(props.context.lang),
        include: INCLUDES_IN_CONTENTFUL,
      });
      return v;
    },
  },
}))(ContentfulConnectorComponent);
