import React from 'react';
import { withButtonStyle } from './with-button-style';

export const COMPONENT_LINK_BUTTON_CONTENT_TYPE = 'link-button';

export interface LinkButtonProps {
  href: string;
  target?: string;
  children: string;
}

class LinkButtonStyleless extends React.Component<LinkButtonProps> {
  render() {
    return <a {...this.props} />;
  }
}

export const LinkButton = withButtonStyle(LinkButtonStyleless, 'LinkButton');
