import React, { ComponentType } from 'react';

import { LinkComponent, LinkModel } from './link';
import { ComponentModel } from '../../common/models';
import { withContentful } from '../../connectors/with-contentful';

import './menu.less';

export const COMPONENT_MENU_CONTENT_TYPE = 'menu';

export interface MenuModel extends ComponentModel {
  type?: string;
  links: LinkModel[];
}

interface MenuProps extends MenuModel {
  onItemClick: () => void;
}

export class MenuComponent extends React.Component<MenuProps, any> {
  state = {};

  onClick = () => this.props.onItemClick && this.props.onItemClick();

  render() {
    const { links } = this.props;
    return (
      <nav>
        <ul className="te-menu">
          {links &&
            links.map((item: LinkModel, idx: number) => (
              <li key={`te-menu-item-${idx}`} className="te-menu-item">
                <LinkComponent {...item} onClick={this.onClick} />
              </li>
            ))}
        </ul>
      </nav>
    );
  }
}

export const Menu: ComponentType<MenuProps> = withContentful()(MenuComponent);
