import React from 'react';
import { IntersectionObserverProps, InView } from 'react-intersection-observer';

interface DelayedInViewProps extends IntersectionObserverProps {
  isOn: boolean;
}

interface DelayedInViewState {
  isOn: boolean;
}

/**
 * Delays initialization of InView via "isOn" prop.
 */
export class DelayedInView extends React.Component<DelayedInViewProps, DelayedInViewState> {
  state = {
    isOn: false,
  };

  static getDerivedStateFromProps(props: DelayedInViewProps, state: DelayedInViewState) {
    return {
      isOn: props.isOn || state.isOn,
    };
  }

  onChange = (inView: boolean, entry: IntersectionObserverEntry): void => {
    const { onChange } = this.props;
    if (inView && !this.state.isOn) {
      this.setState({ isOn: true });
    }
    if (onChange) {
      onChange(inView, entry);
    }
  };

  render() {
    const { isOn, ...restOfProps } = this.props;
    if (!this.state.isOn) {
      return restOfProps.children({ inView: false, ref: () => React.createRef(), entry: undefined });
    }
    return <InView {...restOfProps} onChange={this.onChange} />;
  }
}
