import React, { ComponentType } from 'react';
import { connectedElement, Contextable } from '../../tipser-context';
import { Menu } from '../..';

interface MainMenuProps {
  onClick?: () => void;
}

export class MainMenuNotConnected extends React.Component<Contextable & MainMenuProps> {
  onClick = () => this.props.onClick && this.props.onClick();

  render() {
    const mainMenuProps = this.props.context.site && this.props.context.site.mainMenu;
    return (
      <React.Fragment>
        {this.props.context.site && <Menu {...mainMenuProps} onItemClick={this.onClick} />}
      </React.Fragment>
    );
  }
}

export const MainMenu: ComponentType<MainMenuProps> = connectedElement(MainMenuNotConnected);
