import React, { ComponentType } from 'react';

import { ComponentModel } from '../../common/models';
import { fetchTipserCollection } from '../../api/tipser-api';
import { TipserProductComponent } from '../product/tipser-product';
import { PRODUCT_ALTERNATIVE_RENDERER } from '../product/product-renderer';
import { connectedElement, Contextable } from '../../tipser-context';
import { Loader } from '../../common/loader/loader';
import { getProductModelFromCollectionItemProductModel } from '../../common/utils';
import { withSentry } from '../../common/sentry/with-sentry';

import './collection.less';

export const COMPONENT_TIPSER_COLLECTION_CONTENT_TYPE = 'tipserCollection';

const COLLECTION_ITEMS_BATCH_SIZE = 10;

export interface TipserCollectionItemModel {
  collectionId?: string;
  collectionName?: string;
}

export interface TipserCollectionModel extends ComponentModel {
  name?: string;
  collectionItems?: TipserCollectionItemModel[];
  defaultCollectionId?: string;
  hideMenu?: boolean;
  cssClass?: string;
}

type TipserCollectionComponentProps = TipserCollectionModel & Contextable;

class TipserCollectionComponentNotConnected extends React.Component<TipserCollectionComponentProps, any> {
  constructor(props: TipserCollectionComponentProps) {
    super(props);
    let activeMenuItem = 0;
    props.collectionItems &&
      props.collectionItems.map((item: TipserCollectionItemModel, index) => {
        if (item.collectionId === props.defaultCollectionId) {
          activeMenuItem = index;
        }
      });
    this.state = {
      collection: null,
      isPending: true,
      items: [],
      activeMenuItem: activeMenuItem,
    };
  }

  getIndexForCollection(collectionId: string) {
    const index = this.props.collectionItems.findIndex((item) => item.collectionId === collectionId);
    return Math.max(index, 0);
  }

  componentDidMount() {
    this.changeCollection(this.props.defaultCollectionId, this.getIndexForCollection(this.props.defaultCollectionId));
  }

  changeCollection = (id, index) =>
    this.setState(
      {
        isPending: true,
      },
      () =>
        fetchTipserCollection(id).then((response) => {
          this.setState(
            {
              isPending: false,
              collection: response.data,
              activeMenuItem: index,
              items: response.data.items.slice(0, COLLECTION_ITEMS_BATCH_SIZE), // at the beggining lets render just 10 items
            },
            () => this.loadMore()
          );
        })
    );

  componentDidUpdate() {
    if (
      (!this.state.collection || this.state.collection.id !== this.props.defaultCollectionId) &&
      !this.state.isPending
    ) {
      this.changeCollection(this.props.defaultCollectionId, this.getIndexForCollection(this.props.defaultCollectionId));
    }
  }

  // recursively added more items to be rendered, "dumb" virtualization
  loadMore = () =>
    setTimeout(() => {
      const hasMore = !this.state.collection || this.state.items.length + 1 < this.state.collection.items.length;
      this.state.collection &&
        this.setState((prev) => ({
          items: this.state.collection.items.slice(0, prev.items.length + 10),
        }));
      hasMore && this.loadMore();
    }, 100);

  render() {
    const { isPending, items } = this.state;
    return (
      <div className="tipser-collection gaudy-collection">
        <div className="te-collection">
          {!isPending &&
            items.map((colItem, index) => {
              const product = getProductModelFromCollectionItemProductModel(colItem.product);
              return (
                <React.Fragment key={`collection-product-${index}`}>
                  <TipserProductComponent
                    dontLoadProduct={true}
                    product={product}
                    key={`collection-product-${index}`}
                    productId={colItem.product.id}
                    posId={this.props.context.posId}
                    renderer={PRODUCT_ALTERNATIVE_RENDERER.CARD}
                  />
                </React.Fragment>
              );
            })}
          {isPending && <Loader />}
        </div>
      </div>
    );
  }
}

export const TipserCollectionComponent: ComponentType<TipserCollectionModel> = withSentry(
  connectedElement(TipserCollectionComponentNotConnected)
);
