import React, { ComponentType } from 'react';

import { contentTypeMapper } from '../../common/content-type-mapper';
import { LinkModel } from './link';
import { withContentful } from '../../connectors/with-contentful';
import { ComponentModel } from '../../common/models';

import './horizontal-menu.less';

export const COMPONENT_HORIZONTAL_MENU_CONTENT_TYPE = 'horizontalMenu';

export interface HorizontalMenuModel extends ComponentModel {
  name: string;
  links: LinkModel[];
  cssClass: string;
}

interface HorizontalMenuProps extends HorizontalMenuModel {}

interface HorizontalMenuState {
  selectedIndex: number;
}

export class HorizontalMenuComponent extends React.Component<HorizontalMenuProps, HorizontalMenuState> {
  menuContainer;

  constructor(props: HorizontalMenuProps) {
    super(props);
    this.state = {
      selectedIndex: null,
    };
    this.menuContainer = React.createRef();
  }

  componentDidMount() {
    this.centerSelectedItem();
  }

  componentDidUpdate() {
    this.centerSelectedItem();
  }

  centerSelectedItem() {
    const container = this.menuContainer.current;
    const selectedElement = container.querySelector('.selected');

    if (selectedElement) {
      const itemPositionLeft = selectedElement.offsetLeft;
      const itemWidth = selectedElement.scrollWidth;
      const containerWidth = container.offsetWidth;

      container.scrollLeft = itemPositionLeft + itemWidth / 2 - containerWidth / 2;
    }
  }

  updateSelectedItem(idx) {
    if (this.state.selectedIndex !== idx) {
      this.setState({
        selectedIndex: idx,
      });
    }
  }

  clearSelectedItem() {
    if (this.state.selectedIndex !== null) {
      this.setState({
        selectedIndex: null,
      });
    }
  }

  render() {
    const { links, cssClass } = this.props;
    return (
      <div ref={this.menuContainer} className={'te-horizontal-menu' + (cssClass ? ' ' + cssClass : '')}>
        {links &&
          links.map((link: LinkModel, idx: number) => (
            <span
              className={
                'te-horizontal-menu-item' +
                (this.state.selectedIndex === idx ? ' selected te-horizontal-menu-item-active' : '')
              }
              key={idx}
            >
              {contentTypeMapper(
                { ...link },
                {
                  updateClickedItem: () => this.updateSelectedItem(idx),
                  clearClickedItem: () => this.clearSelectedItem(),
                }
              )}
            </span>
          ))}
      </div>
    );
  }
}

export const HorizontalMenu: ComponentType<HorizontalMenuProps> = withContentful()(HorizontalMenuComponent);
