import React, { ComponentType } from 'react';
import { Contextable, connectedElement, TipserContextData } from './tipser-context';
import { TipserElement } from './components/tipser-element';
import { BrowserRouter, Route, Switch } from 'react-router-dom';
import { PageBySlug } from './components/page/page';

type AppProps = {
  id: string;
  lang?: string;
  basePath?: string;
  options?: TipserContextData;
} & Contextable;

interface AppState {
  lang: string;
}

class AppNotConnected extends React.Component<AppProps, AppState> {
  constructor(props: AppProps) {
    super(props);
    this.state = {
      lang: props.lang,
    };
  }

  componentDidUpdate() {
    if (this.props.lang !== this.state.lang) {
      this.setState({
        lang: this.props.lang,
      });
    }
  }

  render() {
    return (
      <React.Fragment>
        <BrowserRouter basename={this.props.basePath || ''}>
          <Switch>
            <Route path="/" exact render={() => this.props.children || <TipserElement id={this.props.id} />} />
            <Route path="/:slug" exact render={({ match }) => <PageBySlug slug={match.params.slug} />} />
          </Switch>
        </BrowserRouter>
      </React.Fragment>
    );
  }
}

export const App: ComponentType<AppProps> = connectedElement(AppNotConnected);
