import React, { ComponentType } from 'react';
import { connectedElement, Contextable, TipserContextData } from '../../tipser-context';
import { PageComponent } from '../page/page';

export interface RootPageProps extends Contextable {
  options?: TipserContextData;
}

class RootPageNotConnected extends React.Component<RootPageProps> {
  render() {
    const rootPageProps = this.props.context.site && this.props.context.site.rootPage;
    return <React.Fragment>{this.props.context.site && <PageComponent {...rootPageProps} />}</React.Fragment>;
  }
}

export const RootPage: ComponentType<RootPageProps> = connectedElement(RootPageNotConnected);
