import React, { ComponentType } from 'react';
import { Entry } from 'contentful';

import { contentfulClient } from '../../connectors/contentful/contentful-client';
import { INCLUDES_IN_CONTENTFUL, parseContentfulStructure } from '../../connectors/contentful/contentful-parser';
import { connectedElement, Contextable } from '../../tipser-context';
import { RelatedPagesComponent, RelatedPagesModel } from '../..';
import { toContentfulCmsLocale } from '../../common/utils';

import './dynamic-related-pages.less';

export const COMPONENT_DYNAMIC_RELATED_PAGES_CONTENT_TYPE = 'layoutDynamicRelatedContent';

export interface DynamicRelatedPagesModel extends RelatedPagesModel {
  watchedTags: Array<string>;
  orMode: boolean;
  maxItemsNumber?: number;
}

interface DynamicRelatedPagesContainerProps extends DynamicRelatedPagesModel, Contextable {
  id: string;
}

class DynamicRelatedPagesComponentNotConnected extends React.Component<DynamicRelatedPagesContainerProps> {
  state = {
    pages: [],
  };

  static defaultProps = {
    watchedTags: [],
    orMode: false,
    maxItemsNumber: 100,
  };

  componentDidMount(): void {
    contentfulClient(this.props.context)
      .getEntries({
        locale: toContentfulCmsLocale(this.props.context.lang),
        include: INCLUDES_IN_CONTENTFUL,
        [`fields.tag[${this.props.orMode ? 'in' : 'all'}]`]: this.props.watchedTags.join(','),
        content_type: 'page',
        order: '-sys.createdAt',
        limit: this.props.maxItemsNumber,
      })
      .then((data) => {
        const pages = data.items.map((value: Entry<any>) =>
          parseContentfulStructure({
            id: value.sys.id,
            contentType: value.sys.contentType.sys.id,
            ...value.fields,
          })
        );
        this.setState({ pages });
      });
  }

  render() {
    const { orMode, watchedTags, ...theRestOfProps } = this.props;
    return <RelatedPagesComponent pages={this.state.pages} useHeadline={true} {...theRestOfProps} />;
  }
}

export const DynamicRelatedPagesComponent: ComponentType<DynamicRelatedPagesContainerProps> = connectedElement(
  DynamicRelatedPagesComponentNotConnected
);
