import { ComponentType } from 'react';

export const noop = () => {};

export const getProductModelFromCollectionItemProductModel = (colItemProduct) => {
  let product = Object.assign({}, colItemProduct);
  // update price in new product model
  if (colItemProduct.salesPrice.value < colItemProduct.price.value) {
    product['discountPriceIncVat'] = colItemProduct.salesPrice;
    product['priceIncVat'] = colItemProduct.price;
  } else {
    product['priceIncVat'] = colItemProduct.salesPrice;
  }
  return product;
};

export const cssObjectToReactCss = (obj) => {
  const reactCssObj = {};
  Object.keys(obj).forEach((key) => {
    const newKey = key.replace(/-([a-z])/gi, function(g) {
      return g[1].toUpperCase();
    });
    reactCssObj[newKey] = obj[key];
  });
  return reactCssObj;
};

import { bemNamesFactory } from 'bem-names';
export const customBemNamesFactory = (bemClassName) => {
  return bemNamesFactory(bemClassName, {
    separators: { element: '-', modifier: '-' },
  });
};

export const BREAKPOINTS = Object.freeze({
  LARGE: 1200,
  MEDIUM: 990,
  SMALL: 768,
  XSMALL: 480,
});

export enum BACKGROUND_POSITION_OPTIONS {
  TOP = 'top',
  BOTTOM = 'bottom',
  CENTER = 'center',
}

export const shallowObjectCompare = (objA, objB) => {
  const hasOwnProperty = Object.prototype.hasOwnProperty;

  const is = (x, y) => {
    // SameValue algorithm
    if (x === y) {
      // Steps 1-5, 7-10
      // Steps 6.b-6.e: +0 != -0
      return x !== 0 || 1 / x === 1 / y;
    } else {
      // Step 6.a: NaN == NaN
      return x !== x && y !== y;
    }
  };

  if (is(objA, objB)) {
    return true;
  }

  if (typeof objA !== 'object' || objA === null || typeof objB !== 'object' || objB === null) {
    return false;
  }

  var keysA = Object.keys(objA);
  var keysB = Object.keys(objB);

  if (keysA.length !== keysB.length) {
    return false;
  }

  // Test for A's keys different from B.
  for (var i = 0; i < keysA.length; i++) {
    if (!hasOwnProperty.call(objB, keysA[i]) || !is(objA[keysA[i]], objB[keysA[i]])) {
      return false;
    }
  }

  return true;
};

export function getDisplayName<T extends ComponentType>(WrappedComponent: T): string {
  return WrappedComponent.displayName || WrappedComponent.name || 'Component';
}

const addCssLine = (css: string, id: string) => {
  const node = document.createElement('style');
  node.id = id;
  node.innerHTML = css;
  document.body.appendChild(node);
};

const createPrimaryColorStyle = (color: string) => {
  return `.te-primary-fill {
        background-color: ${color};
        color: #fff;
    }
    .te-primary-color {
        color: ${color};
    }
    .te-primary-border {
        border-color: ${color};
    }
    .te-primary-border-hover:hover {
        border-color: ${color};
    }`;
};

import { DEFAULT_PRIMARY_COLOR } from '../tipser-context';
export function primaryColorManager(primaryColor: string) {
  const nodeId = 'primary-color-style';
  return {
    load: () => {
      const isvalidHexColor = (color) => {
        const regexp = /^#([A-Fa-f0-9]{6}|[A-Fa-f0-9]{3})$/;
        const isHex = regexp.test(color);
        return isHex ? color : DEFAULT_PRIMARY_COLOR;
      };
      return addCssLine(createPrimaryColorStyle(isvalidHexColor(primaryColor)), nodeId);
    },
    unload: () => {
      const primaryColorStyle = document.getElementById(nodeId);
      primaryColorStyle && primaryColorStyle.parentNode.removeChild(primaryColorStyle);
    },
  };
}

export const delay = (time: number) => new Promise((resolve) => setTimeout(resolve, time));

export function toContentfulCmsLocale(locale: string): string {
  if (locale === 'de' || locale === 'de-CH') {
    return 'de';
  } else if (locale === 'sv') {
    return 'sv-SE';
  } else if (locale === 'da') {
    return 'da';
  } else if (locale === 'fr' || locale === 'fr-CH' || locale === 'fr-FR') {
    return 'fr';
  } else {
    return locale;
  }
}
